import { Link } from "gatsby";
import * as React from "react";

const ContactUsButton: React.FC<{ primary: boolean }> = (props) => {
  const primary = props.primary ?? true;
  return (
    <Link
      to="/contact"
      className={`text-sm ${
        primary ? `bg-white text-black` : `bg-[#262A2D] text-white`
      } px-6 py-2 rounded-full`}
    >
      Contact Us
    </Link>
  );
};

export default ContactUsButton;
